import React, { useState, useEffect, useContext } from "react";
import RegisterForm from "./RegisterForm";
import { getAllPersons, getOnePerson } from "../../api/person";
import { singUp } from "../../api/auth";
import GlobalContext from "../../context/GlobalContext";

const Register = () => {
  const [contextState] = useContext(GlobalContext);
  const [person, setPerson] = useState("");
  const [user, setUser] = useState("");
  const [autoName, setAutoName] = useState(true);
  const [userName, setUserName] = useState("");
  const [formData, setFormData] = useState({
    id: "",
    username: "",
    password: "",
  });

  const getPerson = (e) => {
    const index = e.target.selectedIndex;
    const el = e.target.childNodes[index];
    const id = el.getAttribute("id");

    setFormData({ id: id });

    getOnePerson(id)
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setUser(res);
        setUserName(res.firstName.split("")[0] + res.lastName.split(" ")[0]);
      })
      .catch((err) => {
        console.error(err.status);
      });
  };

  const handlerInputChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  useEffect(() => {
    let unmounted = false;

    getAllPersons()
      .then((res) => {
        return res.json();
      })

      .then((res) => {
        if (!unmounted) {
          setPerson(res);
        }
      })
      .catch((err) => {
        console.error(err.status);
      });

    return () => {
      unmounted = true;
    };
  }, []);

  const createUser = () => {
    console.log(formData);
    console.log(contextState.userName);
    console.log(userName);

    singUp(
      formData.id,
      formData.username ? formData.username : userName,
      formData.password,
      contextState.userName
    )
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        console.log(res.status);
      })
      .catch((err) => {
        console.error(err.status);
      });
  };

  return (
    <>
      <RegisterForm
        person={person}
        getPerson={getPerson}
        user={user}
        formData={formData}
        handlerInputChange={handlerInputChange}
        setAutoName={setAutoName}
        autoName={autoName}
        createUser={createUser}
      />
    </>
  );
};

export default Register;
